.app__laurels_awards {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex-wrap: wrap;
	margin-top: 3rem;
}

.app__laurels_awards-card {
	flex: 1;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	min-width: 230px;
	margin: 1rem;
}

.app__laurels_awards-card img {
	width: 50px;
	height: 50px;
}
.app__laurels_awards-card_content {
	display: flex;
	flex-direction: column;
	margin-left: 1rem;
}
.app__wrapper_img {
	width: 350px;
	height: 550px;
}
.app__wrapper_img img {
	width: 100%;
	height: 100%;
}

@media (min-width: 1900px) {
	.app__laurels_awards-card {
		min-width: 390px;
	}
	.app__wrapper_img img {
		width: 50%;
		height: 50%;
	}
}

@media (max-width: 1150px) {
	.app__wrapper_img img {
		max-width: 80%;
	}
}
@media (max-width: 450px) {
	.app__laurels_awards-card {
		min-width: 100%;
		margin: 1rem 0;
	}
}
